import uniIcons from '@/components/uni-icons/uni-icons.vue';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import upLoadImg from '../../components/common/up-load-img.vue';
import uniPopup from '../../components/uni-popup/uni-popup.vue';
let changelook = ['所有人可见', '仅自己可见', '选中的朋友可见'];

export default {
	components: {
		uniIcons,
		uniNavBar,
		upLoadImg,
		uniPopup
	},
	data() {
		return {
			secret: '所有人可见',
			text: '',
			imageList: [],
			isget: false //标识符
		};
	},
	//监听页面返回
	onBackPress() {
		//如果页面没有值则不提示保存
		if (!this.text && this.imageList.length < 1) {
			return;
		} else {
			if (!this.isget) {
				this.save();
				return true; //使页面不反回
			}
		}
	},

	methods: {
		//保存草稿
		save() {
			uni.showModal({
				content: '是否要保存为草稿?',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if (res.confirm) {
						console.log('save');
					} else {
						console.log('no');
					}
					this.isget = true; //修改标识符
					//返回
					uni.navigateBack({
						delta: 1
					});
				}
			});
		},
		//返回
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		//发布
		submit() {
			console.log('发布');
		},
		//所有人可见
		changelook() {
			uni.showActionSheet({
				itemList: changelook,
				success: res => {
					this.secret = changelook[res.tapIndex];
					//选中项等于数组索引
				}
			});
		},
		//事件接受子组件传过来的参数
		upload(arr) {
			this.imageList = arr;
		},
		//关弹窗
		close() {
			this.$refs.popup.close();
		}
	}
};
